.footer{
  padding: 3.56em 0 8.2em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 10.9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__col{
    max-width: 25em;
  }
  &__c{
    display: flex;
    align-items: flex-start;
    margin-top: 1.56em;
    &-icon{
      width: 1.25em;
      height: 1.5em;
      flex-shrink: 0;
      margin-right: .06em;
      @include center;
    }
    img{
      width: .75em;
    }
  }
  .text18{
    line-height: 133%;
  }
  &__address{
    margin-top: 1.625em;
  }
  &__links{
    max-width: 11.8em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.56em;
  }
  &__link{
    @include lastM(0 0 .625em);
  }
}

.footer-contacts{
  margin-top: 1.5em;
  &__items{
    margin-top: .625em;
    @include grid100gap(1.56em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  .social{
    margin-left: .625em;
  }
}

.social{
  display: flex;
  align-items: center;
  &__item{
    width: 1.3125em;
    margin-right: .625em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.scroll-up {
  position: fixed;
  width: 3em;
  height: 3em;
  bottom: 2em;
  right: 3.75em;
  z-index: 20;
  opacity: 0;
  pointer-events: none;
  @include center;
  padding: 0;
  &--active {
    opacity: 1;
    pointer-events: unset;
  }
  svg{
    width: 1.625em;
    height: auto;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0 5.125em;
    &__inner{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1.875em 2em;
    }
    &__col{
      &:nth-child(1){
        text-align: center;
        max-width: 100%;
        order: 10;
        grid-column: 1/-1;
      }
      & > .text24{
        font-size: 1.25em;
      }
    }
    &__links{
      margin-top: .5em;
    }
    &__link{
      @include lastM(0 0 .5em);
    }
    &__logo{
      display: none;
    }
    &__c{
      display: block;
      margin-top: .625em;
      &-icon{
        display: inline-block;
      }
    }
  }

  .footer-contacts{
    &__items{
      gap: .625em;
    }
    &__item{
      flex-direction: column;
      align-items: center;
      &:last-child{
        margin-top: 1.875em;
      }
      a{
        font-weight: 700;
      }
    }
    .social{
      margin: 1em 0 0;
      &__item{
        margin: 0 .75em;
        width: 2em;
      }
    }
  }

  .scroll-up{
    bottom: .5em;
    right: .5em;
  }
}
