.news-page{
  padding-bottom: 3.25em;
  &__grid{
    padding: 0 1.25em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (5.62em * 3 / 4)));
    gap: 1.25em 5.62em;
    margin-top: 1.875em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.25em;
  }
  .pagination{
    padding: 0 1.25em;
  }
}

.page-title{
  padding: .625em;
  padding-left: 0;
  @include grid100gap(.625em);
}

.news-item{
  display: grid;
  grid-template-columns: 35.5em calc(100% - 41.125em);
  gap: 5.625em;
  padding: 1.25em 0;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__date{
    margin-bottom: .25em;
  }
  &__text{
    margin-top: .89em;
    &:first-child{
      margin-top: 0;
    }
    p{
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 992px){
  .page-title{
    padding: 0;
    .text34{
      font-size: 1.75em;
      line-height: 183%;
    }
  }

  .news-item{
    padding: 0;
    grid-template-columns: 100%;
    gap: 1em;
    &__date{
      text-align: center;
    }
    .text40{
      font-size: 2em;
      text-align: center;
      letter-spacing: unset;
      max-width: 98%;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .news-page{
    padding-bottom: 0;
    &__grid{
      padding: 0;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    .pagination{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .news-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
