.product-table{
  padding: 1.25em;
  border: 1px solid $dividerGray;
  &__grid{
    display: grid;
    grid-template-columns: 8em 4.5em 16.625em calc(100% - 32.125em);
    gap: 1em;
  }
  .badges2,
  .badges{
    position: static;
    flex-wrap: wrap;
    margin-top: -.5em;
  }
  .badge{
    margin: .5em .5em 0 0;
  }
  .catalog-card__info{
    text-align: left;
    margin: 0;
    gap: .125em;
  }
  .catalog-card__nav,
  .product-props,
  .catalog-card__text{
    margin: 0;
  }
  .badges2{
    position: static;
    flex-direction: row;
  }
  .badge2{
    margin: .5em 0 0;
  }
  .catalog-card__logo{
    position: static;
    width: 100%;
    transform: unset;
  }
  &__th{
    height: 1.875em;
    display: flex;
    align-items: center;
    font-weight: 700;
    &:nth-child(1){
      padding-left: 2.2em;
    }
  }
  &__td {
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    &:nth-child(1){
      padding-left: 1em;
    }
  }
  &._type2{
    @media screen and (min-width: 577px){
      .product-table__groups{
        position: relative;
        min-height: 6.25em;
      }
      .table-header2{
        grid-template-columns: 8em 4.5em 10em 10em 4em 4em 4em 4.5em  calc(100% - 57em);
      }
      .product-table__td,
      .product-table__th{
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7),
        &:nth-child(8),
        &:nth-child(9){
          text-align: center;
          justify-content: center;
        }
      }
      .product-row{
        padding-left: 9em;
        grid-template-columns: 4.5em 21em 4em 4em 4em 4.5em  calc(100% - 48em);
        &._center{
          align-items: center;
          min-height: 5.75em;
        }
        &._first{
          &~.product-row{
            border: none;
            .product-table__td{
              padding-top: 0;
              padding-bottom: 0;
              &:nth-child(1){
                padding-left: 0;
              }
              &:nth-child(2){
                padding-left: 11em;
              }
              &:nth-child(3),
              &:nth-child(4){
                text-align: center;
              }
            }
          }
          .product-table__td{
            padding-bottom: .25em;
            &:nth-child(1){
              position: absolute;
              top: 0;
              left: 0;
              width: 8em;
            }
            &:nth-child(3){
              position: absolute;
              top: 0;
              left: 14.5em;
              width: 10em;
            }
            &:nth-child(4){
              padding-left: 11em;
            }
          }
        }
      }
      &._tabs{
        .table-header2{
          grid-template-columns: 8em 16em 11em 4em 4em 4em 4.5em calc(100% - 58.5em);
        }
        .product-table__td,
        .product-table__th{
          &:nth-child(4){
            text-align: center;
            justify-content: center;
          }
        }
        .product-row{
          grid-template-columns: 8em 28em 4em 4em 4em 4.5em calc(100% - 58.5em);
          padding-left: 0;
          &._center{
            align-items: center;
            min-height: 5.75em;
          }
          &._first{
            &~.product-row{
              .product-table__td{
                &:nth-child(1){
                  padding-left: 2.31em;
                }
                &:nth-child(2){
                  padding-left: 17em;
                }
              }
            }
            .product-table__td{
              padding-bottom: .25em;
              &:nth-child(1){
                position: static;
                width: 100%;
                padding-left: 2.31em;
              }
              &:nth-child(2){
                position: absolute;
                top: 0;
                left: 9em;
                width: 16em;
              }
              &:nth-child(3){
                position: static;
                width: 100%;
                padding-left: 17em;
              }
              &:nth-child(4){
                padding-left: 0;
                text-align: center;
              }
            }
          }
        }
      }
    }

  }
  &._type3{
    @media screen and (min-width: 577px){
      .table-header2,
      .product-row{
        align-items: center;
        grid-template-columns: 8em 4.5em calc(100% - 45.5em) 7em 7em 7em 7em;
      }
      .product-table__td,
      .product-table__th{
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7){
          text-align: center;
          justify-content: center;
        }
      }
      ._props-col{
        grid-column: 4/-1;
      }
      .pair-row{
        grid-template-columns: repeat(4,calc(25% - 3em / 4));
        gap: 1em;
        text-align: center;
        &__col{
          padding: 0;
        }
        .cartcalc{
          margin: 0 auto;
        }
      }
      &._tabs{
        .table-header2{
          grid-template-columns: 6em 23.5em 7em 7em 7em 7em;
        }
        .product-table__td,
        .product-table__th{
          &:nth-child(3){
            text-align: center;
            justify-content: center;
          }
        }
        ._props-col{
          grid-column: 3/-1;
        }
        .product-row{
          grid-template-columns: 6em 23.5em 7em 7em 7em 7em;
          padding-left: 0;
          &._center{
            align-items: center;
            min-height: 5.75em;
          }
          &._first{
            &~.product-row{
              .product-table__td{
                &:nth-child(1){
                  padding-left: 2.31em;
                }
                &:nth-child(2){
                  padding-left: 17em;
                }
              }
            }
            .product-table__td{
              padding-bottom: .25em;
              &:nth-child(1){
                position: static;
                width: 100%;
                padding-left: 2.31em;
              }
              &:nth-child(2){
                position: static;
                top: 0;
                left: 9em;
                width: 16em;
              }
              &:nth-child(3){
                padding-left: 0;
              }
              &:nth-child(4){
                padding-left: 0;
                text-align: center;
              }
            }
          }
        }
      }
    }
  }
  &._tabs{
    .product-table__groups:not(._active){
      display: none !important;
    }
  }
}


.table-header{
  .favourite-btn{
    border: none;
    margin-top: -.25em;
  }
  &__col{
    display: flex;
    justify-content: space-between;
    &:nth-child(1){
      grid-column: 1/3;
    }
    &:nth-child(2){
      display: grid;
      grid-template-columns: 13em 14em 10em calc(100% - 46em) 5em;
      gap: 1em;
      @media screen and (min-width: 577px){
        grid-column: 3/-1;
        align-items: flex-start;
      }
    }
  }
  &__info{
    display: flex;
    justify-content: space-between;
  }
}

.table-header2{
  margin-top: .675em;
}

.table-header2,
.product-row{
  border-top: 1px solid $dividerGray;
}

.product-row{
  &__title{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .product-table{
    @media screen and (min-width: 577px){
      font-size: .675em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__tables{
      grid-template-columns: 100%;
      gap: 1em;
      width: calc(100% + 2em);
      margin-left: -1em;
    }
  }

  .product-table{
    .catalog-table__media{
      height: auto;
    }
    .catalog-table__img{
      margin-top: 0;
      img{
        border-radius: .25em;
        border: 2px solid $primary;
      }
    }

    .product-row{
      &__title{
        flex-shrink: 0;
        width: 5.625em;
        margin-right: .25em;
        display: block;
        color: $textLightGray;
      }
    }
    .catalog-table__models{
      display: flex;
      flex-wrap: wrap;
    }
    .catalog-table__models-item{
      font-size: 0.75em;
      line-height: 150%;
      margin-right: 1em;
    }
    .product-table__td{
      &._hide-mob{
        display: none !important;
      }
    }
    .cartcalc{
      width: 100%;
      height: 2.75em;
      grid-template-columns: 2.75em calc(100% - 5.5em) 2.75em;
      input{
        font-size: 1.125em;
      }
      button{
        font-size: 1em;
      }
      svg{
        width: .875em;
      }
    }
  }

  .table-header{
    grid-template-columns: 100%;
    gap: .5em;
    padding-right: 5em;
    position: relative;
    &__col{
      &:nth-child(1){
        grid-column: 1/-1;
      }
      &:nth-child(2){
        grid-template-columns: 100%;
        gap: .5em;
      }
    }
    .catalog-card__logo{
      position: absolute;
      top: 0;
      right: 0;
      width: 5em;
      height: 2em;
      display: flex;
      justify-content: flex-end;
      img{
        width: auto;
        max-width: 100%;
        max-height: 100%;
      }
    }

    .badges2{
      position: absolute;
      right: 0;
      top: 3em;
      width: 2em;
    }

    .product-props{
      display: none;
    }

    &__info{
      justify-content: unset;
    }
    .favourite-btn{
      margin-left: .5em;
    }
  }

  .table-header2{
    display: none;
  }

  .product-table._type1{
    padding: 1.5em 1em;

    .catalog-card__nav{
      grid-column: 1/-1;
      width: 100%;
      display: grid;
      grid-template-columns: calc(100% - 3.25em) 2.75em;
      gap: .5em;
      align-items: center;
    }
    .catalog-card__btn{
      margin: 0;
      height: 2.5em;
      span{
        font-size: 1.125em;
      }
    }
    .product-table__td{
      position: relative;
      padding: 0;
      display: flex;
      &:nth-child(1){
        padding-left: 0;
        position: absolute;
        top: 1em;
        left: 0;
        width: 3.125em;
      }
      &:nth-child(4){
        width: calc(100% + 4.125em);
        margin-left: -4.125em;
        margin-top: 1em;
      }
    }
    .product-row{
      margin-top: 1.5em;
      padding-top: 1em;
      grid-template-columns: 100%;
      gap: 0;
      padding-left: 4.125em;
      position: relative;
      &__desc{
        width: calc(100% - 5.875em);
      }
    }
  }

  .product-table._type2,
  .product-table._type3{
    align-self: flex-start;
    .product-table__rows{
      margin-top: 1.5em;
    }
    .product-table__td{
      padding: 0;
      display: flex;
    }
    .catalog-table__img{
      img{
        border: none;
      }
    }
    .product-row__title{
      color: $black;
    }
    .product-table__td {
      &._img-col {
        grid-column: 1/2;
        grid-row: 1/2;
      }

      &._desc-col {
        grid-column: 2/3;
        grid-row: 1/2;
      }
    }
    ._sum-col,
    ._box-col,
    ._count-col{
      .product-row__title{
        width: 100%;
        font-weight: 800;
        margin-bottom: .5em;
        .text12{
          font-size: .875em;
        }
      }
    }
    ._box-col{
      flex-direction: column;
      width: calc(100% + 4em);
      .product-row__desc{
        .text12{
          color: $primary;
        }
        .text10{
          color: rgba($black,.8);
          text-decoration: unset;
          span{
            text-decoration: line-through;
          }
        }
        .text12,
        .text10{
          font-size: 1.125em;
          &:after{
            content: 'бел.руб.';
            font-size: .75em;
            font-weight: 400;
            color: rgba($black,.8);
            margin-left: .125em;
          }
        }
      }
    }
    ._sum-col{
      margin-top: .5em;
      justify-content: space-between;
      .product-row__title{
        width: auto;
      }
      .catalog-table__desc{
        display: flex;
        align-items: center;
        &:after{
          content: 'бел.руб.';
          font-size: .75em;
          color: rgba($black,.8);
          margin-left: .2em;
        }
        .text12,
        .text10{
          font-size: 1em;
        }
        .text10{
          margin-left: .25em;
          color: rgba($black,.8);
        }
      }
    }
    .product-row{
      padding: 1em 0;
      grid-template-columns: 5.625em calc(100% - 6.625em);
      row-gap: .5em;
    }
    .catalog-group__colors{
      margin-top: 1.625em;
    }
    &._tabs{
      .product-table__rows{
        margin-top: 1em;
      }
      .product-table__td{
        &._desc-col{
          margin-bottom: .5em;
          padding-left: 0;
          grid-column: 1/-1;
        }
      }
      ._props-col{
        border-top: none;
      }
      .pair-row{
        margin-top: .5em;
      }
    }
  }

  .product-table._type2{

    .product-table__td{
      &._model-col,
      &._size-col,
      &._couple-col,
      &._sum-col{
        grid-column: 1/-1;
      }

      &._count-col{
        padding-left: 3.625em;
        flex-direction: column;
      }
      &._model-col{
        .product-row__title,
        .catalog-table__models-item{
          font-weight: 800;
          color: $black;
        }
      }

      &._couple-col{
        .catalog-table__desc .text12{
          display: flex;
          &:after{
            content: 'бел.руб.';
            margin-left: .2em;
          }
        }
      }
    }

  }

  .product-table._type3{
    ._props-col{
      grid-column: 1/-1;
      display: block;
      border-top: 1px solid $dividerGray;
    }
    .pair-row{
      margin-top: 1.5em;
      grid-template-columns: 9.25em calc(100% - 10.25em);
      gap: .5em 1em;
      &__col{
        padding: 0;
        text-align: left;
      }
    }
    ._sum-col,
    ._size-col{
      grid-column: 1/-1;
    }
    ._sum-col{
      display: flex;
      justify-content: space-between;
    }
    ._size-col{
      display: flex;
      align-items: center;
      .text14{
        font-weight: 700;
      }
      .product-row__title{
        width: auto;
        .text14{
          font-weight: 600;
        }
      }
    }
  }

  .catalog-group__bottom{
    @include grid100gap(1em);
    &-right{
      @include grid100gap(1em);
    }
    .catalog-card__btn{
      margin: 0;
      width: 100%;
      height: 2.5em;
      span{
        font-size: 1.125em;
      }
    }
  }

}
