.shoe-card{
  border-bottom: 1px solid $dividerGray;
  border-right: 1px solid $dividerGray;
  position: relative;
  padding-bottom: 1.25em;
  @media screen and (min-width: 993px){
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3){
      border-top: 1px solid $dividerGray;
    }
    &:nth-child(3n - 2){
      border-left: 1px solid $dividerGray;
    }
  }
  &__sum{

  }
}

.shoe-tabs{
  position: absolute;
  height: 13.75em;
  width: 2.5em;
  top: 2.31em;
  left:  1.25em;
  padding-bottom: 2.5em;
  &._remove-arrows{
    .swiper-slide-duplicate{
      display: none;
    }
    .swiper-button{
      display: none;
    }
  }
  .swiper{
    &-container{
      height: 100%;
      overflow: hidden;
    }
    &-slide{
      height: 2.81em;
      padding-bottom: .31em;
    }
    &-button{
      position: absolute;
      width: 2.5em;
      height: 2.5em;
      left: 0;
      bottom: 0;
      top: unset;
      margin: 0;
      @include center;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
  &__btn{
    cursor: pointer;
    transition: all .3s;
    width: 2.5em;
    height: 2.5em;
    position: relative;
    border-radius: 0.25em;
    border: 1px solid #CECECE;
    &._active,
    &:hover{
      border-color: $primary;
    }
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      border-radius: inherit;
    }
  }
}

.shoe-content{
  display: none;
  &._active{
    display: block;
  }
  &__top{
    text-align: center;
    padding: 0 2.5em 0 4.37em;
  }
  .card-slider{
    width: 100%;
    height: auto;
  }
}

.shoe-article{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 1em;
  padding: 0 3.12em;
  &__btn{
    margin: .5em 1em 0 0;
    height: 1.5em;
    display: flex;
    align-items: center;
    font-weight: 700;
    border-bottom: 1px dashed $black;
    transition: all .3s;
    position: relative;
    span{
      font-size: .9375em;
      line-height: 160%;
    }
    &._viewed{
      border-color: $primary;
      &:after{
        content: '';
        position: absolute;
        width: 4px;
        height: 4px;
        left: 100%;
        top: 50%;
        transform: translateY(-50%);
        margin-left: .25em;
        border-radius: 50%;
        background-color: $primary;
      }
    }
    &._active{
      border-color: transparent;
      pointer-events: none;
      span{
        font-size: 1.25em;
        line-height: 120%;
      }
    }
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
}

.variant-sum,
.article-content{
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    .article-content__btn{
      margin: 0;
    }
  }
  &__btn{
    padding: 0 .5em;
    margin-top: .625em;
    width: 100%;
    height: 2em;
  }
}

.article-content{
  display: none;
  margin-top: .5em;
  &._active{
    display: block;
  }
  &__top{
    padding: 0 3.125em;
  }
  &__title{
    text-align: center;
  }
  &__more{
    margin-top: .25em;
    display: flex;
    justify-content: center;
    font-weight: 500;
  }
  &__bottom{
    margin-top: 1.125em;
    padding: 0 1.25em;
  }
  &__nav{
    margin-top: .625em;
  }
  .badges2{
    top: .25em;
    right: .75em;
  }
  .badge{
    margin-right: .125em;
  }
  .cartcalc{
    position: relative;
  }
  .m-tooltip{
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1px;
    transition: all .3s;
    &:after{
      content: '';
      display: block;
      margin-top: -.0625em;
      width: 0.75em;
      height: 0.375em;
      background-image: url(../img/triangle.svg);
      @include bgImgSize(100%);
    }
    &__inner{
      padding: .375em .75em;
      border-radius: 0.1875em;
      background-color: $dividerGray;
      min-width: 7.875em;
    }
  }
}

.shoe-size{
  margin-top: .5em;
}

.shoe-price{
  margin-top: .5em;
  display: flex;
  align-items: center;
  &__num,
  &__num2{
    margin-right: .5em;
  }
  &__num2{
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%) rotate(-5deg);
      height: 1px;
      width: calc(100% + 2px);
      background-color: $black;
    }
  }
}

.shoe-counts{
  margin-top: .5em;
  display: flex;
  justify-content: space-between;
  &__nums{
    padding-left: .5em;
  }
  .text12{
    letter-spacing: unset;
  }
  .cartcalc{
    margin-bottom: .125em;
    margin-left: auto;
  }
}

.shoe-sum{
  display: flex;
  align-items: center;
  &__title{
    margin-right: .5em;
  }
  .shoe-price{
    margin: 0;
    &__num,
    &__num2{
      margin-right: .25em;
    }
  }
}

.shoe-all-sum{
  width: calc(100% - 2.5em);
  margin: 1.25em auto 0;
  padding-top: .625em;
  border-top: 1px solid $dividerGray;
  @include grid100gap(.25em);
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .shoe-price{
    margin: 0;
  }
}

.shoe-counts2{
  .size{
    flex-wrap: wrap;
  }
  .size-item{
    margin: .5em .25em 0 0;
  }
}


@media screen and (max-width: 992px){
  .shoe-card{
    @media screen and (min-width: 577px){
      &:nth-child(1),
      &:nth-child(2){
        border-top: 1px solid $dividerGray;
      }
      &:nth-child(odd){
        border-left: 1px solid $dividerGray;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .shoe-card{
    border-left: none;
    border-right: none;
    &:nth-child(1){
      border-top: 1px solid $dividerGray;
    }
  }
}


