.blog{
  padding-bottom: 5.3em;
  &__items{
    margin: 2.94em auto 0;
    width: 100%;
    max-width: 68em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 6.75em));
    gap: 3.75em 13.5em;
  }
  .pagination{
    width: 100%;
    max-width: 68em;
    margin-left: auto;
    margin-right: auto;
    padding-left: 3em;
  }
}

.blog-nav{
  margin-top: 1.875em;
  padding-bottom: 1.5em;
  position: relative;
  @include center;
  flex-wrap: wrap;
  &:after{
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: 45.75em;
    max-width: 100%;
    height: 1px;
    background-color: $dividerGray;
  }
  &__link{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 600;
    margin-top: .625em;
    &:not(:last-child){
      &:after{
        content: '';
        flex-shrink: 0;
        width: 2px;
        height: 1em;
        background-color: #E2E2E2;
        margin: 0 1em;
      }
    }
    span{
      font-size: 1.25em;
      line-height: 150%;
      letter-spacing: 0.05625em;
    }
  }
}

.blog-slider{
  overflow: hidden;
  width: 43.75em;
  max-width: 100%;
  margin: 2.25em auto 0;
}

.blog-card{
  &__img{
    @include imgCover(63.6%);
  }
  &__type{
    margin-top: .94em;
    text-align: center;
    text-transform: uppercase;
  }
  &__info{
    @include grid100gap(.56em);
    text-align: center;
    margin-top: 1.44em;
    .text24{
      text-transform: uppercase;
    }
  }
  &__desc{
    margin-top: 1.25em;
    text-align: center;
    line-height: 187%;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: .55em;
  }
  &__tag{
    margin: .19em 1.5em 0 0;
    color: #8E8E8E;
    span{
      font-size: .875em;
      line-height: 191%;
      letter-spacing: 0.05625em;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
}

.blog-item{
  .blog-nav{
    margin-top: 1em;
  }
  &__inner{
    margin: 0 auto;
    width: 69.375em;
    max-width: 100%;
  }
  &__top{
    margin-top: 1.25em;
    .text48{
      text-align: center;
    }
    .text{
      margin-top: 1.69em;
    }
  }
  .images__item{
    margin: 1.81em 0;
  }
  &__quote{
    margin: 2.63em 0 0;
    background: #EDEDED;
    padding: 1.4em 5.2em;
    border-left: .5em solid $primary;
  }
  &__p{
    margin: 1.33em 0 0;
  }
  .images{
    margin-top: 1.69em;
  }
  &__products{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .125em));
    gap: .85em .25em;
    width: 62.125em;
    max-width: 100%;
    margin: 0 auto;
  }
}

.blog-product{
  text-align: center;
  display: block;
  &__img{
    height: 10.9375em;
    margin-bottom: .25em;
    @include center;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__title{
    font-size: 0.8125em;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
}

.share{
  margin-top: 3.83em;
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .25em;
    &:hover{
      img{
        transform: translateY(-25%);
      }
    }
    img{
      width: 100%;
      transition: all .4s;
    }
  }
}

.news-card2{
  .text24{
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding-bottom: 3em;
    .page-title{
      padding: 0;
    }
    &__items{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 2em 1.5em;
    }
    .pagination{
      display: none;
    }
    .load-more{
      margin-top: 1.5em;
    }
  }

  .blog-nav{
    margin-top: 0;
    padding: 0 0 1em;
    &:after{
      //display: none;
      width: 16.5em;
    }
  }

  .blog-slider{
    margin-top: 1.5em;
  }

  .blog-card{
    &__type{
      margin-top: 1em;
    }
    &__info{
      margin-top: 1em;
    }
    &__tags{
      margin-top: .69em;
      justify-content: center;
    }
  }

  .blog-item{
    padding-bottom: 1em;
    &__top{
      margin-top: 1.5em;
      .text48{
        font-size: 2em;
      }
      .text{
        margin-top: 1em;
      }
    }
    .images__item{
      margin: 1.5em 0 .5em;
    }
    &__quote{
      padding: .875em 1.5em;
      text-align: center;
      border-width: .25em;
      width: calc(100% + 2em);
      margin-left: -1em;
      margin-top: 1.5em;
    }
    .images._double{
      width: calc(100% + 2em);
      max-width: unset;
      margin-left: -1em;
      margin-top: 0;
    }
    &__products{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1em 2em;
    }
  }

  .blog-collection{
    padding: 1em 0 3em;
    .slider{
      margin-top: 2.5em;
    }
  }

  .news-slider{
    .swiper{
      &-container{
        width: calc(100% + 1.875em);
      }
      &-slide{
        width: 50%;
        padding-right: 1.875em;
      }
    }
  }

  .news-slider2{
    .swiper-slide{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .blog-collection{
    .text35{
      text-align: center;
      font-size: 2.25em;
    }
  }

  .news-slider{
    .swiper{
      &-slide{
        //width: 90%;
      }
    }
  }

  .news-card2{
    .news-card__content{
      margin-top: 1em;
      gap: 1em;
      text-align: center;
    }
  }
}
