.search{
  padding: 0 0 3.125em;
  overflow: hidden;
  .catalog__items{
    margin-top: 1.25em;
  }
  @media screen and (min-width: 993px){
    .catalog__items{
      grid-template-columns: repeat(4,25%);
    }
    .shoe-card,
    .catalog-item{
      &:nth-child(4){
        border-top: 1px solid $dividerGray;
      }
      &:nth-child(3n - 2){
        border-left: none;
      }
      &:nth-child(4n - 3){
        border-left: 1px solid $dividerGray;
      }
    }
  }
}

.search-top{
  @include grid100gap(.5em);
  padding: 1em 2em 2em;
  background-color: $bgGray;
  .text16{
    line-height: 188%;
    a{
      color: $primary;
      border-bottom: 1px dashed $primary;
      &:hover{
        border-color: transparent;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 10.0625em) 9.0625em;
    gap: 1em;
  }
  .fg{
    input{
      font-size: 2.125em;
      font-weight: 700;
      height: 2.09em;
      border-radius: .23em;
      padding: 0 .5em;
      background-color: #fff;
    }
  }
  &__btn{
    height: 4.4375em;
    font-weight: 600;
    span{
      font-size: 2.125em;
      line-height: 150%;
    }
  }
}

.search-info{
  margin-top: 2.625em;
  @include grid100gap(.625em);
  .text14{
    b{
      font-weight: 700;
    }
  }
}

.search-links{
  margin-top: .625em;
  &__current{
    display: none;
  }
  &__items{
    display: flex;
  }
  &__item{
    border-bottom: 2px solid transparent;
    padding: 0 1.25em;
    height: 3.125em;
    display: flex;
    align-items: center;
    font-weight: 600;
    &._active,
    &:hover{
      color: $black;
      border-color: $primary;
    }
    span{
      font-size: 1em;
      line-height: 189%;
      letter-spacing: 0.05625em;
    }
  }
}


@media screen and (max-width: 992px){
  .search-top{
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 1em 1em 2em;
    &__grid{
      grid-template-columns: calc(100% - 3.75em) 2.75em;
      gap: 1em;
    }
    .fg{
      input{
        height: 2.75em;
        font-size: 1em;
      }
    }
    &__btn{
      height: 2.75em;
      padding: 0;
      background-image: url(../img/search.svg);
      @include bgImgSize(1.125em);
      span{
        display: none;
      }
    }
  }

  .search-info{
    .text34{
      font-size: 1.75em;
    }
  }

  .search-links{
    position: relative;
    ._active{

    }
    &__current{
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-radius: 0.5rem;
      border: 2px solid #D0D5DD;
      background-color: #fff;
      box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
      height: 2.75em;
      padding: 0 .875em;
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1.125em;
        height: 1.125em;
        background-image: url(../img/select-arrow.svg);
        @include bgImgSize(100%);
      }
    }
    &__items{

    }
  }

  .search-links__current._active{
    &~.tabs-slider,
    &~.search-links__items{
      opacity: 1;
      pointer-events: unset;
    }
  }

  .tabs-slider .tab,
  .search-links__item{
    height: unset;
    padding: 0;
    border: none;
    @include lastM(0 0 1em);
    &._active{
      background-image: url(../img/check3.svg);
      background-repeat: no-repeat;
      background-position: right center;
      background-size: 1em auto;
    }
  }

  .tabs-slider .tab{
    span{
      font-size: 1em;
    }
  }

  .contacts-page__nav .tabs-slider,
  .search-links__items{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: 1px;
    width: 100%;
    padding: 1.5em 1em;
    box-shadow: 0px 16px 16px 0px rgba(0, 0, 0, 0.10), 0px 8px 8px 0px rgba(0, 0, 0, 0.05);
    background-color: #fff;
    flex-direction: column;
    z-index: 13;
    max-height: 21em;
    overflow-y: auto;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }

  .contacts-page__nav{
    position: relative;
    .tabs-slider{
      display: block;
    }
    .swiper{
      &-container{
        display: block;
      }
      &-wrapper{
        transform: unset !important;
        flex-direction: column;
      }
      &-slide{
        @include lastM(0 0 1em);
      }
      &-nav{
        display: none;
      }
    }
  }
}
