.m-text h2,
.text48{
  font-size: 3em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text45{
  font-size: 2.8125em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text40{
  font-size: 2.5em;
  line-height: 120%;
  letter-spacing: 0.05625em;
}

.text35{
  font-size: 2.1875em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text34{
  font-size: 2.125em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text32{
  font-size: 2em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text28{
  font-size: 1.75em;
  line-height: 133%;
  letter-spacing: 0.05625rem;
}

.text24{
  font-size: 1.5em;
  line-height: 125%;
  letter-spacing: 0.05625rem;
}

.m-text h5,
.text20{
  font-size: 1.25em;
  line-height: 120%;
  letter-spacing: 0.05625rem;
}

.text li,
.text p,
.text18{
  font-size: 1.125em;
  line-height: 167%;
  letter-spacing: 0.05625em;
}

.text17{
  font-size: 1.0625em;
  line-height: 176%;
  letter-spacing: 0.05625em;
}

.text16{
  font-size: 1em;
  line-height: 151%;
}

.m-text h6,
.text15{
  font-size: .9375em;
  line-height: 160%;
}

.text14{
  font-size: .875em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text13{
  font-size: .8125em;
  line-height: 125%;
  letter-spacing: 0.05625em;
}

.text12{
  font-size: .75em;
  line-height: 150%;
  letter-spacing: 0.05625em;
}

.text10{
  font-size: .625em;
  line-height: 120%;
  letter-spacing: 0.05625em;
}

.medium-text{
  font-weight: 500;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.extra-bold-text{
  font-weight: 800;
}

.light-gray-text{
  color: $textLightGray;
}

.medium-gray-text{
  color: $textMediumGray;
}

._center-text{
  text-align: center;
}

.primary-text{
  color: $primary;
}

.black80-text{
  color: rgba($black,.8);
}

.line-through{
  text-decoration: line-through;
}


@media screen and (max-width: 992px){
  .text48{
    font-size: 2em;
    line-height: 187%;
  }

  .text28-mob{
    font-size: 1.75em;
  }

  .text24-mob{
    font-size: 1.5em;
    line-height: 133%;
  }

  .text20-mob{
    font-size: 1.25em;
    line-height: 120%;
  }

  .text18-mob{
    font-size: 1.125em;
    line-height: 167%;
  }

  .text14-mob{
    font-size: .875em;
  }

  .text li,
  .text p,
  .text18{
    line-height: 144%;
  }
}
