.auth{
  padding-bottom: 3.75em;
}

.auth-form{
  margin-top: 1.25em;
  &__nav{
    display: flex;
    justify-content: center;
    padding-top: .5em;
  }
  .fg{
    textarea{
      height: 5em;
    }
  }
  &__check{
    margin-top: 1.5em;
  }
}

.form-item,
.form-items{
  padding: 1.25em 0;
  border-bottom: 1px solid #E4E7EC;
  &:last-child{
    padding-bottom: 0;
    border: none;
  }
}

.form-items{
  @include grid100gap(1.25em);
  .form-item{
    padding: 0;
    border: none;
  }
}

.form-item{
  display: grid;
  grid-template-columns: 17.5em calc(100% - 19.5em);
  gap: 2em;
  &__title{
    padding: .625em 0;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
    max-width: 45em;
    width: 100%;
    ._col2{
      grid-column: span 2;
    }
    ._col3{
      grid-column: 1/-1;
    }
  }
  &__links{
    display: flex;
    align-items: center;
    padding: .625em 0;
  }
  &__link{
    color: $primary;
    margin-right: 2em;
    &:hover{
      color: $black;
    }
  }
  &__btn{
    border-radius: 0.25em;
    padding: 0 1em;
    height: 2.5em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
  &._pt0{
    padding-top: 0;
  }
}


@media screen and (max-width: 992px){
  .form-item{
    grid-template-columns: 100%;
    gap: 0;
    &__fields{
      grid-template-columns: 100%;
      gap: .5em;
      ._col2{
        grid-column: 1/-1;
      }
    }
    &__title{
      padding-top: 0;
      &._hide-mob{
        display: none;
      }
    }
    &__links{
      padding: 0;
      justify-content: space-between;
    }
    &__link{
      margin-right: 0;
    }
    &__btn{
      width: 100%;
    }
    &__title{
      &._hide-mob{
        display: none;
      }
    }
  }

  .auth{
    .form-items{
      padding-top: 0;
      &._pb0{
        padding-bottom: 0;
        border-bottom: none;
      }
    }
    .form-item{
      padding: 0;
      border: none;
      &._border-top{
        border-top: 1px solid #E4E7EC;
      }
    }
  }

  .auth-form{
    margin-top: 2.5em;
    @include grid100gap(1.25em)
  }
}
