.shoe-table{
  padding: 1.25em;
  border: 1px solid $dividerGray;
  .catalog-group__colors{
    padding-left: 2.37em;
  }
  .product-table__groups{
    &:not(._active){
      display: none;
    }
  }
  &__head{
    margin-top: .625em;
    padding-top: .5em;
    padding-bottom: .5em;
    border-top: 1px solid $dividerGray;
    border-bottom: 1px solid $dividerGray;
  }
  &__grid{
    display: grid;
    align-items: center;
    gap: .375em ;
  }
  &__th{
    font-weight: 500;
    span{
      font-size: .625em;
      line-height: 180%;
      letter-spacing: 0.025em;
    }
  }
  &__title{
    display: none;
  }
  &__desc{

  }
  &__rows{
    @include grid100gap(.25em);
    padding: 1.53em 0 1.25em;
    border-bottom: 1px solid $dividerGray;
  }
  &__badges{
    display: flex;
    .badge{
      height: 1.125em;
      padding: 0 .25em;
      min-width: 2em;
      justify-content: center;
      @include lastM(0 .25em 0 0);
    }
  }
  .catalog-group__bottom{
    border-top: none;
  }
  .catalog-card__buttons{
    display: flex;
  }
  &__info{
    margin-right: 1.63em;
    @include grid100gap(.25em);
    .catalog-group__total{
      margin: 0;
    }
  }
  @media screen and (min-width: 993px){
    .catalog-card__btn{
      margin-left: .5em;
    }
    .catalog-group__total{
      margin-right: 1em;
    }
    ._hide-desktop{
      display: none;
    }
    &__prices{
      .text16{
        display: none;
      }
    }
    &__prices .text10,
    &__price2{
      position: relative;
      display: inline-flex;
      &:after{
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) rotate(-5deg);
        height: 1px;
        width: calc(100% + 2px);
        background-color: gray;
      }
    }
    &._type1{
      .cartcalc{
        display: inline-grid;
      }
      .shoe-table{
        &__grid{
          grid-template-columns: 6.5em 10.25em calc(100% - 51.225em) 8.8em 10em 13.8em;
        }
        &__th,
        &__td{
          &:nth-child(1){
            padding-left: 2.31em;
          }
          &:nth-child(4),
          &:nth-child(5),
          &:nth-child(6){
            text-align: center;
          }
          &:nth-child(6){
            padding-right: 3.5em;
          }
        }
      }
    }
    &._type2{
      .catalog-group__bottom-right{
        margin-left: auto;
      }
      .shoe-table{
        &__grid{
          grid-template-columns: 6.5em 10.25em calc(100% - 42.875em) 25em;
        }
        &__th,
        &__td{
          &:nth-child(1){
            padding-left: 2.31em;
          }
          &:nth-child(3),
          &:nth-child(4){
            text-align: center;
          }
        }
      }
      .shoe-table__rows{
        padding: 0 0 1em;
      }
      .size{
        justify-content: center;
      }
    }
    &._type3{
      .cartcalc{
        display: inline-grid;
      }
      .shoe-table{
        &__grid{
          grid-template-columns: 6.5em 11.5em calc(100% - 54em) 5.25em 6em 5.75em 5.5em 6em 4.5em;
        }
        &__th,
        &__td{
          &:nth-child(1){
            padding-left: 2.31em;
          }
          &:nth-child(4),
          &:nth-child(5),
          &:nth-child(6),
          &:nth-child(7),
          &:nth-child(8),
          &:nth-child(9){
            text-align: center;
          }
          &:nth-child(6){
          }
        }
      }
    }
    &._type4{
      .catalog-group__bottom-right{
        margin-left: auto;
      }
      .shoe-table{
        &__grid{
          grid-template-columns: 6.5em 10.25em calc(100% - 51.875em) 8.625em 25em;
        }
        &__th,
        &__td{
          &:nth-child(1){
            padding-left: 2.31em;
          }
          &:nth-child(3),
          &:nth-child(4),
          &:nth-child(5){
            text-align: center;
          }
        }
      }
      .shoe-table__rows{
        padding: 0 0 1em;
      }
      .size{
        justify-content: center;
      }
    }
  }
}

.shoe-header{
  display: grid;
  grid-template-columns: 12.75em 13em calc(100% - 43.25em) 1.5em 6em 5em;
  gap: 1em;
  .badges,
  .badges2{
    position: static;
  }
  .badges2{
    @media screen and (min-width: 993px){
      display: flex;
      align-items: center;
      justify-content: flex-end;
      flex-direction: row;
      height: 2em;
      .badge2{
        margin: 0;
        @include lastM(0 .5em 0 0);
      }
    }
  }
  .badge{
    margin-right: .125em;
  }

  &__info{
    @include  grid100gap(.125em);
  }
  &__logo{
    display: flex;
    justify-content: flex-end;
    img{
      max-width: 100%;
      max-height: 1.25em;
    }
  }
}

.badge{
  &._brown{
    background: #FF6B00;
  }
}

.shoe-footer{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 1em;
  padding-top: 1em;
  border-top: 1px solid $dividerGray;
  &__prices,
  &__left,
  &__right{
    display: flex;
    align-items: center;
  }
  &__left{
    margin-right: 3em;
  }
  &__prices{
    margin-left: .31em;
  }
  .shoe-price__num,
  .shoe-price__num2{
    margin-right: .125em;
  }
  .shoe-sum{
    margin-right: 3em;
  }
  .shoe-price{
    margin-top: 0;
  }
}


@media screen and (max-width: 992px){
  .catalog__tables{
    &._tables{
      @media screen and (min-width: 577px){
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1em;
      }
    }
  }

  .shoe-table{
    padding: 1.5em 1em;
    .catalog-group__colors{
      padding: 1.5em 0 0;
    }
    &__head{
      display: none;
    }
    &__rows{
      margin-top: 1em;
      padding: 0;
      border: none;
      gap: 0;
    }
    &__row{
      padding: 1.5em 0;
      grid-template-columns: 100%;
      border-top: 1px solid $dividerGray;
      &:last-child{
        border-bottom: 1px solid $dividerGray;
      }
    }
    &__td{
      display: grid;
      align-items: center;
      grid-template-columns: 7.5em calc(100% - 7.5em);
    }
    ._count,
    ._model{
      .semi-bold-text,
      .shoe-table__title{
        font-weight: 800;
      }
    }
    ._badges{
      display: none;
    }
    &__title{
      display: block;
    }
    &__desc{
      display: flex;
      align-items: center;
      .shoe-table__badges{
        margin-left: 1em;
      }
    }
    .catalog-card__buttons{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    .catalog-card__btn{
      height: 2em;
      span{
        font-size: .875em;
      }
    }
    &__prices{
      .text16{
        display: block;
        opacity: 0.8;
        margin-left: .125em;
      }
      .text12,
      .text10{
        font-size: 1.125em;
        line-height: 133%;
      }
      .text12{
        color: $primary;
      }
      .text10{
        opacity: .8;
      }
    }
    &__price,
    &__price2{
      display: flex;
    }
    &__info{
      gap: .5em;
    }
    .catalog-group__bottom,
    .catalog-group__bottom-right{
      @include grid100gap(1em);
    }
    .catalog-group__bottom .catalog-card__btn{
      margin: 0;
      width: 100%;
      height: 2.5em;
    }
    &._type1,
    &._type3{
      .cartcalc{
        max-width: 10.25em;
        width: 100%;
        height: 2.75em;
        grid-template-columns: 2.75em calc(100% - 5.5em) 2.75em;
        input{
          font-size: 1.125em;
        }
        button{
          font-size: 1em;
        }
        svg{
          width: .875em;
        }
      }
    }
    &._type2,
    &._type4{
      ._count{
        grid-template-columns: 100%;
        .shoe-table__title{
          display: none;
        }
      }
    }
    &._type3{
      .shoe-table__row{
        grid-template-columns: repeat(2,calc(50% - .5em));
        column-gap: 1em;
        align-items: flex-start;
      }
      .shoe-table__td{
        grid-column: 1/-1;
      }
      ._sum{
        display: none;
      }
      ._price,
      ._count{
        grid-column: unset;
        grid-template-columns: 100%;
        gap: .5em;
        .shoe-table__title{
          font-weight: 700;
        }
      }
    }
    &._type4{
      ._price{
        align-items: flex-start;
      }
    }
  }

  .shoe-header{
    grid-template-columns: 100%;
    gap: .5em;
    padding-right: 5em;
    position: relative;
    &__logo{
      position: absolute;
      right: 0;
      top: 0;
      max-width: 5em;
      display: flex;
      justify-content: flex-end;
    }
    .badges2{
      position: absolute;
      top: 2.5em;
      right: 0;
    }
    &__props{
      display: none;
    }
  }

  .shoe-footer{
    padding: 1em;
    border-radius: 0.25em;
    border: 1px solid $dividerGray;
    display: block;
    &__left,
    &__right{
      margin: 0;
      justify-content: space-between;
    }
  }
}
