.shoe-variants{
  margin-top: .625em;
  @include grid100gap(1em);
  &__sum{
    margin-top: 1.125em;
    &-item{
      display: flex;
      .text14{
        margin-right: .35em;
        line-height: 172%;
      }
    }
  }
}

.variant-card{
  padding-bottom: 1em;
  border-bottom: 1px solid $dividerGray;
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    border-bottom: 1px dashed transparent;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      width: .375em;
      height: .375em;
      left: 100%;
      top: 50%;
      transform: translateY(-50%);
      margin-left: .25em;
      border-radius: 50%;
      background-color: $primary;
      opacity: 0;
    }
    &._viewed{
      border-color: $primary;
      margin-right: .625em;
      &:after{
        opacity: 1;
      }
    }
  }
  .product-props{
    margin: 0 0 0 .625em;
    &__item{
      margin: 0 .5em 0 0;
    }
  }
  .shoe-table__badges{
    margin-top: .5em;
  }
  &__prices{
    margin-top: .5em;
    @include grid100gap(.125em);
  }
  .shoe-price{
    margin: 0;
  }
  .shoe-counts{
    margin-top: .5em;
    justify-content: unset;
    align-items: center;
    .cartcalc{
      margin: 0;
    }
  }
  .shoe-counts2{
    margin-top: .5em;
  }
  &__grid{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .variant-card__top{
      width: 13.78em;
      max-width: 100%;
    }
    .shoe-table__badges{
      margin: 0;
    }
    .shoe-counts{
      margin: 0;
      width: 16em;
      max-width: 100%;
      justify-content: flex-end;
    }
  }
}

.variant-sum{
  margin-top: 1em;
  padding: 1em;
  border-radius: .25em;
  border: 1px solid $dividerGray;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .5em));
  gap: 1em;
  align-items: center;
  .shoe-all-sum{
    margin: 0;
    padding: 0;
    border: none;
    width: 100%;
    display: block;
  }
  .shoe-sum{
    justify-content: space-between;
  }
  &__btn{
    margin: 0;
  }
  &._type2{
    @media screen and (min-width: 577px){
      display: flex;
      gap: unset;
      .variant-sum__col{
        @include lastM(0 1em 0 0);
      }
      .variant-sum__btn{
        white-space: nowrap;
        padding: 0 1em;
        @include lastM(0 .5em 0 0);
      }
      .variant-sum__nav{
        display: flex;
        gap: unset;
      }
    }
  }
}

.variant-hint{
  padding: 1.875em 0 1.25em;
  a{
    color: $primary;
    white-space: nowrap;
    &:hover{
      text-decoration: underline;
    }
  }
}

.catalog-item2__desc{
  max-width: 100%;
}

.catalog-item2__info{
  flex-grow: 1;
  &-badges{
    display: flex;
    position: static;
    margin-left: .625em;
  }
  &-top{
    white-space: nowrap;
    display: flex;
    align-items: center;

    .badge{
      margin-right: .5em;
    }
  }
}

.shoes{
  padding: 1em;
  border-radius: .25em;
  border: 1px solid $dividerGray;
}


@media screen and (max-width: 576px){
  .variant-card{
    &__grid{
      @include grid100gap(.5em);
      .shoe-counts{
        justify-content: unset;
      }
    }
  }

  .variant-sum{
    margin-top: .625em;
    grid-template-columns: 100%;
  }

  .variant-hint{
    padding: .625em 0;
  }

  .catalog-item2__info{
    &-top{
      display: block;
    }
    &-badges{
      margin: .5em 0 0;
    }
  }
}
