.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  padding-bottom: 3.125em;
  z-index: 20;
  transition: all .4s;
  &._transform{
    transform: translateY(-100%);
    @media screen and (min-width: 993px){
      padding-bottom: 0;
      &:before{
        display: none;
      }
      .header-user .container,
      & > .container{
        max-width: 100%;
        padding: 0 1em;
      }
      .header__logo{
        order: -1;
        width: 8.2em;
        margin-left: -.5em;
      }
      .header-contacts{
        display: none;
      }
      .header-auth__btn{
        height: 4.375em;
      }
      .menu{
        position: static;
        .container{
          padding: 0;
        }
      }
      .menu-item__link, .menu-item__show{
        padding: 0 .94em;
        height: 5em;
      }
      .header-search__field{
        width: 13.5em;
        input{
          padding-left: .5em;
        }
      }
      .menu-item__hidden{
        transform: translateY(-2em);
        padding-top: 2em;
      }
    }
  }
  &._fix{
    transform: translateY(0);
    position: fixed;
    width: 100%;
    box-shadow: $shadow;
  }
  &:not(._loaded){
    .menu{
      opacity: 0;
    }
  }
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    bottom: 3.125em;
    background: #E6E6E6;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 13.125em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.mob-menu{
  display: flex;
  align-items: center;
  .header-action__lang{
    display: none;
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__email,
  &__phone,
  &__call{
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  &__phone{
    display: flex;
    align-items: center;
    font-weight: 700;
    span{
      font-size: 1em;
      line-height: 200%;
    }
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .5em;
    }
  }
  &__call{
    margin-left: 1em;
  }
  &__email{
    width: 1.5em;
    margin-left: 2.37em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__lang{
    font-size: 1em;
    line-height: 200%;
    margin-left: .875em;
    ._active{
      font-weight: 700;
    }
  }
}

.header-auth{
  position: relative;
  margin-left: .625em;
  &__btn{
    width: 5em;
    height: 5em;
    @include center;
    position: relative;
    svg{
      width: 1.5em;
      height: auto;
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: .25em;
      background-color: $primary;
      opacity: 0;
      transition: inherit;
    }
    &._not-login{
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
    &._logged{
      &:hover{
        path{
          stroke: $primary;
        }
      }
    }
  }
  &__menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    width: 14em;
    padding: 1.5em;
    background-color: #fff;
    box-shadow: 0 1em 1em 0 rgba(0, 0, 0, 0.10), 0 .5em .5em 0 rgba(0, 0, 0, 0.05);
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 10;
    margin-top: 1px;
    @include grid100gap(1em);
  }
  &__link{
    font-size: 1.125em;
    line-height: 133%;
    color: $primary;
    &:hover{
      color: $black;
    }
  }
  &._active{
    ._not-login{
      path{
        fill: $primary;
      }
      &:before{
        opacity: 1;
      }
    }
    .header-auth__menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-search{
  position: relative;
  &__field{
    position: absolute;
    z-index: 2;
    right: -.625em;
    top: 50%;
    transform: translateY(-50%);
    width: 20em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    input{
      width: 100%;
      height: 2.5em;
      border: none;
      border-bottom: 1px solid $black;
      font-size: 1em;
      letter-spacing: 0.05625em;
      padding-right: 2.5em;
    }
    .header-search__btn{
      position: absolute;
      top: 50%;
      right: .625em;
      transform: translateY(-50%);
    }
  }
  &__btn{
    position: relative;
    z-index: 3;
    width: 1.875em;
    padding: .375em;
    svg{
      width: 100%;
      height: auto;
    }
    ._view2{
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
    }
  }
  &._active{
    ._show-search{
      opacity: 0;
      pointer-events: none;
    }
    .header-search__field{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-user{
  background: $black;
  color: #fff;
  &__inner{
    height: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__right,
  &__left,
  &__items{
    display: flex;
    align-items: center;
  }
  &__lk,
  &__notification,
  &__item{
    font-size: 1em;
    line-height: 200%;
    letter-spacing: 0.05625rem;
    font-weight: 700;
  }
  &__notification,
  &__item{
    margin-right: 2em;
  }
  &__notification{
    opacity: 0.5;
  }
  &__logout{
    width: 1.5em;
    margin-left: .75em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__lk{
    padding: 0 .875em;
    height: 2.5em;
    margin-left: .75em;
    @include center;
  }
  &__cart{
    @include center;
    width: 3.25em;
    height: 2.5em;
    position: relative;
    svg{
      width: 1.5em;
      height: auto;
    }
    span{
      display: none;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
}

.cart-pin{
  position: absolute;
  top: .25em;
  left: 1.625em;
  background-color: #fff;
  border-radius: .375em;
  height: .75em;
  padding: 0 .1875em;
  display: flex;
  align-items: center;
  color: $black;
  font-weight: 800;
  span{
    display: block;
    font-size: .5em !important;
  }
}

.menu{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  &__inner{
    display: flex;
    justify-content: center;
  }
}

.header-cart{
  width: 5em;
  height: 5em;
  @include center;
  position: relative;
  svg{
    width: 1.375em;
    height: auto;
  }
  .cart-pin{
    top: 1.625em;
    background-color: $primary;
    color: #fff;
    right: 1.25em;
    left: unset;
  }
}

.menu-item{
  &._small{
    position: relative;
  }
  &._show{
    .menu-item__show:before{
      opacity: 1;
    }
    .menu-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__link,
  &__show{
    height: 3.125em;
    padding: 0 1.25em;
    font-weight: 600;
    @include center;
    transition: all .3s;
    a,
    span{
      letter-spacing: 0.05625rem;
      font-size: 1.25em;
      line-height: 150%;
    }
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__show{
    position: relative;
    z-index: 11;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: .25em;
      background-color: $primary;
      transition: inherit;
      opacity: 0;
    }
  }
  &__hidden{
    width: 100%;
    z-index: 10;
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: -1px;
      padding-top: 1px;
      display: block !important;
    }
  }
  &__toggle{
    display: none;
  }
}

._showww{
  opacity: 1;
  pointer-events: unset;
}

.menu-dropdown2{
  @media screen and (min-width: 993px){
    border-top: 1px solid $dividerGray;
    background-color: #fff;
    box-shadow: 0px 16px 16px 0px rgba(0, 0, 0, 0.10), 0px 8px 8px 0px rgba(0, 0, 0, 0.05);
    width: 14em;
    &__link{
      &:hover,
      &._active{
        color: #fff;
        background-color: $primary;
      }
    }
  }
  &__link{
    display: flex;
    align-items: center;
    padding: .25em 1.5em;
    min-height: 3em;
    span{
      font-size: 1.125em;
      line-height: 133%;
      letter-spacing: 0.05625em;
    }
  }
}

.menu-dropdown{
  @media screen and (min-width: 993px){
    border-top: 1px solid #E6E6E6;
    background: #FFF;
    box-shadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.30);
  }
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__col{
    padding: 2em 2em 2em 0;
    &:nth-child(2){
      padding-left: 5.5em;
      border-left: 1px solid #E8E8E8;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 200%;
    letter-spacing: 0.05625em;
    text-transform: uppercase;
    font-weight: 600;
    color: $primary;
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    margin-top: 1.625em;
  }
  &__banner{
    margin-left: 4em;
    width: 19.375em;
    padding-bottom: 1em;
  }
}

.menu-links{
  margin-top: .5em;
  &._col2{
    column-count: 2;
    column-gap: 2em;
    max-width: 37em;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    padding: 0 1.25em;
    height: 2em;
    display: flex;
    align-items: center;
    letter-spacing: 0.05625em;
    @include lastM(0 0 .625em);
    &-title{
      font-size: .875em;
      //line-height: 143%;
    }
    &-num{
      font-size: .75em;
      color: $textMediumGray;
      margin-left: .2em;
    }
    &.mt52{
      margin-top: 3.25em;
    }
  }
}

.banner{
  display: block;
  img{
    width: 100%;
    display: block;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding-bottom: 0;
    &:before{
      display: none;
    }
    &__inner{
      padding: .25em 0;
    }
    &__logo{
      width: 8.25em;
    }
    &._open-menu{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .mob-menu{
        transform: translateX(0);
      }
    }
  }

  .header-user{
    &__inner{
      height: 3em;
    }
    &__left{
      display: none;
    }
    &__right{
      width: 100%;
      justify-content: flex-end;
    }
    &__cart{
      margin-right: auto;
      width: unset;
      height: unset;
      font-weight: 700;
      color: $primary;
      svg{
        margin-right: .5em;
      }
      span{
        display: block;
        font-size: 1em;
      }
      .cart-pin{
        top: -.25em;
        left: 1em;
      }
    }
  }

  .header-user ~ .container .mob-menu{
    height: calc(100 * var(--vh) - 6em);
    top: 6em;
  }

  .mob-menu{
    position: fixed;
    top: 3em;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 3em);
    overflow-y: auto;
    background-color: #fff;
    border-top: 1px solid $dividerGray;
    flex-direction: column;
    padding: 1em 0 2em;
    transform: translateX(-150%);
    transition: all .3s;
    .header-action__lang{
      display: block;
      margin-right: auto;
      margin-left: 1.5em;
      margin-top: 1.5em;
    }
  }

  .header-action{
    &__lang{
      display: none;
    }
  }

  .header-cart{
    width: 1.5em;
    height: auto;
    margin-left: 2em;
    z-index: 2;
    svg{
      width: 100%;
    }
    .cart-pin{
      top: -.25em;
      left: 100%;
      right: unset;
      margin-left: -.5em;
    }
  }

  .header-auth{
    margin-left: 2em;
    position: static;
    &__btn{
      width: 1.5em;
      height: auto;
      &:before{
        bottom: -.75em;
        width: 2.5em;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    &__menu{
      margin: 0;
      border-top: 1px solid $dividerGray;
      width: 100%;
    }
  }

  .header-search{
    margin-left: 1.375em;
    position: static;
    &__field{
      right: 0;
      top: 100%;
      width: 100%;
      transform: unset;
      height: 3em;
      box-shadow: 0px 16px 16px 0px rgba(0, 0, 0, 0.10), 0px 8px 8px 0px rgba(0, 0, 0, 0.05);
      background-color: #fff;
      input{
        border-bottom: none;
        border-top: 1px solid $dividerGray;
        height: 100%;
        padding-left: 1em;
      }
      .header-search__btn{
        right: 1em;
      }
    }
    &__btn{
      width: 1.25em;
      padding: 0;
      &:hover{
        path{
          fill: $black;
        }
      }
    }
    &._active{
      ._show-search{
        pointer-events: unset;
        opacity: 1;
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    width: 1.25em;
    height: 1.25em;
    display: block;
    flex-shrink: 0;
    position: relative;
    img{
      transition: all .3s;
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    order: -1;
    position: static;
    .container{
      padding: 0 1.5em;
    }
    &__inner{
      display: block;
    }
  }

  .menu-item{
    &._show{
      .menu-item__toggle{
        background-image: url(../img/minus3.svg);
      }
    }
    &__link,
    &__show{
      padding: 0;
      height: 3.5em;
      justify-content: space-between;
    }
    &__show{
      &:before{
        display: none;
      }
    }
    &__toggle{
      display: flex;
      width: 1.5em;
      height: 1.5em;
      border-radius: 50%;
      flex-shrink: 0;
      background-color: $primary;
      background-image: url(../img/plus2.svg);
      @include bgImgSize(.625em);
    }
    &__hidden{
      padding: 1em 0;
      display: none;
      .container{
        padding: 0;
      }
    }
  }

  .menu-dropdown2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      padding: 0;
      min-height: 2em;
      @include lastM(0 0 .625em);
      span{
        font-size: .875em;
      }
    }
  }

  .menu-dropdown{
    &__col{
      padding: 0 .3125em 0 0;
      &:nth-child(2){
        padding-left: 1em;
      }
    }
    &__bottom{
      display: block;
    }
    &__banner{
      padding: 0;
      margin: 1.625em 0 0;
      width: 100%;
    }
  }

  .menu-links{
    &._col2{
      column-count: 1;
    }
    &__item{
      padding: 0;
    }
  }

  .header-contacts{
    margin-top: .525em;
  }
}
