.lk-filter{
  margin: 1.25em 0 1.5em;
  &__top{
    display: flex;
  }
  .catalog__sort{
    order: unset;
    margin: 0;
    min-width: 10em;
    .nice-select{
      border-width: 1px;
      display: flex;
      align-items: center;
      height: 1.5em;
      &:before{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/filter-icon.svg);
        @include bgImgSize(100%);
        width: 0.75em;
        height: 0.625em;
        margin-right: .5em;
      }
      .list{
        min-width: 100%;
        width: auto;
      }
    }
  }
  &__refresh{
    margin-left: 1em;
    font-weight: 700;
    display: none;
    span{
      font-size: .875em;
      line-height: 153%;
    }
  }
  &__toggle{
    padding: 0 .94em;
    height: 1.5em;
    border: $border3;
    display: flex;
    align-items: center;
    margin-left: 1.875em;
    font-weight: 500;
    svg{
      width: 1em;
      margin-right: .25em;
    }
    &-title{
      font-size: .875em;
    }
    &-count{
      min-width: 1.5em;
      @include center;
      display: none;
      height: 1.5em;
      font-size: .5em;
      line-height: 120%;
      font-weight: 800;
      color: #fff;
      background-color: $primary;
      border-radius: 50%;
      margin-left: .5em;
      flex-shrink: 0;
    }
    &:hover{
      border-color: $primary;
    }
    &._open{
      background-color: $dividerGray;
      border-color: $dividerGray;
      color: $textMediumGray;
      path{
        stroke: $textMediumGray;
      }
    }
    &._selected{
      color: $primary;
      path{
        stroke: $primary;
      }
      .lk-filter__toggle-count{
        display: flex;
      }
      &~.lk-filter__refresh{
        display: flex;
      }
    }
  }
  &__bottom{
    display: none;
    padding: 1.25em;
    margin-top: .625em;
  }
}

.filter{
  &__grid{
    display: grid;
    grid-template-columns: 10.625em 14.375em 17.5em calc(100% - 47em);
    gap: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
    .m-btn{
      margin-right: 1em;
    }
  }
}

.filter-item{
  &__top{
    margin-bottom: .25em;
  }
  .fg{
    input,
    .nice-select{
      border-width: 1px;
    }
    &._right{
      input{
        text-align: right;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(50% - .4375em) .875em calc(50% - .4375em);
  }
  &__split{
    @include center;
  }
}

.orders{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3));
  margin-bottom: 2em;
  @media screen and (min-width: 993px){
    .order-card{
      &:nth-child(3n-1),
      &:nth-child(3n){
        border-left: none;
      }
      &:nth-child(3)~.order-card{
        border-top: none;
      }
    }
  }
}

.order-card{
  background: rgba(255, 255, 255, 0.05);
  border: $border2;
  padding: 3.125em 1.875em 1.875em;
  position: relative;
  &__top{
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 1.06em;
  }
  &__badge{
    height: 2em;
    display: flex;
    align-items: center;
    padding: 0 .81em;
    background-color: $yellow;
    font-weight: 700;
    margin-left: -1px;
    span{
      font-size: 1.125em;
      line-height: 133%;
      letter-spacing: 0.05625em;
    }
    &._blue{
      background-color: #2D9CDB;
      color: #fff;
    }
    &._green{
      background-color: #219653;
      color: #fff;
    }
  }
  &__notification{
    margin-top: .12em;
    width: 1.5em;
    position: relative;
    &._has-message{
      .order-card__notification-badge{
        display: flex;
      }
      path{
        fill: #2B2F33;
      }
    }
    svg{
      width: 100%;
    }
    &-badge{
      position: absolute;
      right: 0;
      bottom: 0;
      min-width: 0.75em;
      height: 0.75em;
      padding: 0 .125em;
      @include center;
      display: none;
      background-color: $primary;
      border-radius: 50%;
      color: #fff;
      span{
        font-size: .5em;
      }
    }
  }
  &__name{
    text-align: center;
  }
  &__images{
    @include center;
    margin: 2.5em 0;
  }
  &__img{
    margin: 0 .15625em;
    width: 2.5em;
    height: 2.5em;
    border-radius: 0.25em;
    border: $border3;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__text{
    text-align: center;
    @include grid100gap(.25em);
  }
  &__props{
    margin: .5em 0 0 auto;
    @include grid100gap(.5em);
    max-width: 15em;
  }
  &__prop{
    font-size: 0.875em;
    line-height: 128%;
    letter-spacing: 0.05625em;
  }
}




@media screen and (max-width: 992px){
  .lk-filter{
    margin-top: .625em;
  }

  .filter{
    &__nav,
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__nav{
      display: grid;
      width: 50%;
      margin-left: auto;
      margin-right: auto;
      .m-btn{
        margin: 0;
      }
    }
  }

  .orders{
    grid-template-columns: repeat(2,calc(50%));
    margin-bottom: .5em;
    @media screen and (min-width: 577px){
      .order-card{
        &:nth-child(2n-1),
        &:nth-child(2n){
          border-left: none;
        }
        &:nth-child(2)~.order-card{
          border-top: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .lk-filter{
    &__top{
      justify-content: space-between;
    }
    .catalog__sort{
      margin-right: .25em;
    }
    &__toggle{
      margin-left: 0;
    }
  }

  .filter{
    &__grid{
      grid-template-columns: 100%;
    }
    &__nav{
      width: 100%;
    }
  }

  .orders{
    grid-template-columns: 100%;
    .order-card{
      &:nth-child(1)~.order-card{
        border-top: none;
      }
    }
  }
}
