.m-text{
  b{
    font-weight: 700;
  }
  h1,
  h2,
  h3,
  h4{
    font-weight: 500;
    letter-spacing: 0.05625em;
  }
  h1{
    font-size: 4em;
    line-height: 110%;
    margin-bottom: .5em;
  }
  h2{
    margin-bottom: .67em;
  }
  h3{
    font-size: 2.25em;
    line-height: 135%;
    margin-bottom: .75em;
  }
  h4{
    font-size: 1.875em;
    line-height: 135%;
    margin-bottom: .75em;
  }
  h5{
    margin-bottom: .75em;
    b{
      font-weight: 900;
    }
  }
  h6{
    margin-bottom: .75em;
    b{
      font-weight: 900;
    }
  }
  hr{
    height: 1.875em;
    display: block;
    opacity: 0;
  }
  p,
  li{
    font-size: 1.125em;
    line-height: 167%;
    font-weight: 500;
    position: relative;
  }
  p{
    @include lastM(0 0 .75em);
  }
  ul,
  ol{
    @include grid100gap(.125em);
  }
  ul{
    li{
      padding-left: 1.55em;
      &:before{
        content: '';
        position: absolute;
        left: .67em;
        top: .83em;
        width: .17em;
        height: .17em;
        background-color: $black;
        border-radius: 50%;
      }
    }
  }
  ol{
    counter-reset: section;
    li{
      counter-increment: section;
      padding-left: .5em;
      &:before{
        content: counter(section) '. ';
        min-width: 1.05em;
      }
    }
  }
}

.qa-card{
  @include lastM(0 0 2em);
  border-bottom: 1px solid $dividerGray;
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    display: flex;
    justify-content: space-between;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__btn{
    width: 3em;
    height: 3em;
    margin: .56em 0 .56em 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1.375em;
    line-height: 150%;
    font-weight: 600;
  }
  &__hidden{
    margin-bottom: 1.5em;
    display: none;
  }
  .text{
    p{
      @include lastM(0 0 1.25em);
    }
  }
}


@media screen and (max-width: 992px){
  .m-text{
    h1{
      font-size: 2em;
    }
    h2{
      font-size: 1.875em;
    }
    h3{
      font-size: 1.625em;
    }
    h4{
      font-size: 1.25em;
    }
    h5{
      font-size: 1em;
    }
    h6{
      font-size: .875em;
    }
  }
}
